.breadcrumb-container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    /* Removed default list style */
    list-style: none;
    padding: 0;
    margin: 0;

    li {
        display: flex;
        align-items: center;
    }
    .breadcrumb-separator {
        color: var(--text-color-1);
    }
    .breadcrumb-item {
        color: var(--text-color-1);
    }
    .current {
        font-weight: 600;
        color: var(--text-color-2);
    }
}
