@import "../../../helpers/styles.scss";

.material-planner-layout {
  display: flex;
  flex-direction: column;
  // height: calc(100vh - 98px);
  width: auto;
  gap: 1rem;

  --primary-color: #fa7474;
  --secondary-color: #ffd6b0;
  --tertiary-color: #e9efc5;
  --text-color-1: #333333;

  /* Resets */
  h6,
  p {
    margin: 0;
    padding: 0;
  }

  .material-planner-layout-header {
    display: flex;
    flex-direction: column;
    align-items: start;
    // justify-items: baseline;
    width: calc(100vw - 200px);
    box-sizing: border-box;
    gap: 1vw;

    .overview-heading {
      margin-right: auto;
      font-weight: 600;
      font-size: 1.6vw;
    }
    .overview-metrics-container {
      display: flex;
      gap: 1.5vw;
    }
    .overview-metrics-box {
      display: flex;
      align-items: center;
      gap: 1vw;
      padding: 1vw;
      border-radius: 0.35vw;

      p {
        margin: 0;
        padding: 0;
        color: var(--text-color-1);
        font-size: 1.4vw;
      }

      p:last-child {
        font-weight: 600;
      }
    }
    .overview-last-refresh {
      display: flex;
      gap: 0.25rem;
      .leftPart {
        width: -webkit-fill-available; /* Safari and Chrome */
        width: -moz-available; /* Firefox */
        width: fill-available; /* Standard property */
        display: flex;
        flex-direction: column;
        gap: 0.75vw;
        font-size: 1vw;
      }
      .rightPart {
        width: 25%;
      }
    }
  }

  /* Body */
  .material-planner-layout-body {
    height: 100%;
    display: flex;
    flex-direction: column;

    .pagination-controls {
      display: flex;
      justify-content: space-around;
      margin-top: 2vw;

      .page-details {
        display: flex;
        align-items: center;
        gap: 1vw;
        font-size: 1.1vw;

        .page-number-input {
          padding: 0.5vw;
          text-align: center;
          font-weight: 500;
          width: 5vw;
          border-radius: 0.25vw;
          border: 1px solid grey;
          font-size: 1.1vw;
        }
      }
    }
  }
}

.orderDetailsContainer {
  display: flex;
  width: 94%;
  height: 100%;
  margin: 2.76vw;
  font-size: 1.4vw;
  .leftPart,
  .rightPart {
    width: 30%;
    display: flex;
    flex-direction: column;
  }

  .rightPart {
    width: 70%;
  }
  .orderDetailKeyList,
  .orderDetailValueList {
    list-style: none;
    line-height: 3.5vw;
    color: black;
    font-weight: 500;
  }

  .orderDetailValueList {
    font-weight: 700;
  }
}

/* Action Modal Styles */
.action_modal__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
}

form.failure-reason-form {
  display: flex;
  align-items: baseline;
  gap: 1vw;
  input {
    margin-left: 0.5vw;
  }
  .checkbox-items-container {
    margin-left: 0.75vw;
    margin-right: 0.75vw;
    display: flex;
    gap: 0.5vw;
    align-items: center;
    justify-content: center;
  }
}

.failure-submit-success-message {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .material-planner-layout {
    display: flex;
    flex-direction: column;
    // height: calc(100vh - 98px);
    width: auto;
    gap: 1rem;

    --primary-color: #fa7474;
    --secondary-color: #ffd6b0;
    --tertiary-color: #e9efc5;
    --text-color-1: #333333;

    /* Resets */
    h6,
    p {
      margin: 0;
      padding: 0;
    }

    .material-planner-layout-header {
      .overview-heading {
        font-size: 2.2vw;
      }
      .overview-metrics-box {
        border-radius: 1vw;

        p {
          font-size: 2vw;
        }
      }
      .overview-last-refresh {
        .leftPart {
          font-size: 1.6vw;
        }
      }
    }

    /* Body */
    .material-planner-layout-body {
      .pagination-controls {
        .page-details {
          font-size: 1.8vw;

          .page-number-input {
            font-size: 1.8vw;
          }
        }
      }
    }
  }

  .orderDetailsContainer {
    display: flex;
    width: 94%;
    height: 100%;
    margin: 2.76vw;
    font-size: 2.2vw;
    .leftPart,
    .rightPart {
      width: 30%;
      display: flex;
      flex-direction: column;
    }

    .rightPart {
      width: 70%;
    }
    .orderDetailKeyList,
    .orderDetailValueList {
      list-style: none;
      line-height: 4vw;
      color: black;
      font-weight: 500;
    }

    .orderDetailValueList {
      font-weight: 700;
    }
  }

  /* Action Modal Styles */
  .action_modal__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    padding: 2rem;
  }

  form.failure-reason-form {
    display: flex;
    align-items: normal;
    gap: 1vw;
    input {
      margin-left: 0.5vw;
    }
    .checkbox-items-container {
      flex-direction: column;
      margin-left: 0;
      margin-right: 0;
    }
  }
}
